import {ChangeDetectionStrategy, Component, HostBinding, Input, ViewEncapsulation} from '@angular/core';
import {AdaptWorkflowCustomize} from '../workflow.model';

@Component({
  selector: 'adapt-workflow-dot, button[adapt-workflow-dot]',
  template: `
      <span *ngIf="label">{{label}}</span>
      <ng-content></ng-content>
  `,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class AdaptWorkflowDotComponent implements AdaptWorkflowCustomize {
  @Input() mainCls: string;
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('active-dot') active: boolean = false;
  @Input() completed: boolean = false;

  @Input()
  get label(): string | number {
    return this._label;
  }

  set label(v: string | number) {
    this._label = v;
  }

  @HostBinding('class')
  get hostClasses(): string {
    return [
      'a-workflow__dot',
      this.active ? 'is-active shadow-1' : '',
      this.label ? 'is-floating' : '',
      this.completed ? 'is-completed' : '',
      this.mainCls
    ].join(' ');
  }

  private _label: string | number;
}
